<template>
  <div>
    <header>
      <div class="header_tou">
        <p>吃的香</p>
        <p style="font-size:10px;color:#83C2FC">欢迎光临吃的香，请随意选择，各个香喷喷 </p>
      </div>
    </header>
   <section>
     <div class="contents">
     <lib-tab></lib-tab>
     <router-view></router-view>
     </div>
   </section>
   <footer>
     <div>
       <cart></cart>
     </div>
   </footer>
  </div>
</template>

<script>
import Cart from './Cart'
import LibTab from './LibTab'
import Foods from './Foods'
export default {
  name: 'HelloWorld',
  components:{
    LibTab,
    Foods,
    Cart
  },
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
